export default class Menu {
  constructor(options) {
    this._el = options.el;
    this._mainMenu = document.querySelector('.main__menu');

    this._el.addEventListener('click', this. _onMenuIconClick.bind(this));
  }

  _onMenuIconClick(event) {
    let iconToggle = event.target.closest('#icon-toggle');
   if(!iconToggle) {
     return;
   }
    this._mainMenu.classList.toggle('js-menu');
    iconToggle.classList.toggle( "active" );
  }
};



